import {Injectable} from '@angular/core';
import {HttpClient} from "@angular/common/http";
import {environment} from "../../environments/environment";
import {Observable} from "rxjs";
import {serverResponse} from "../models/order_details.model";

@Injectable({
  providedIn: 'root'
})
export class OrderService {
  products: ProductResponseModel[] = [];
  private url = environment.serverURL;

  constructor(private http: HttpClient) {
  }

  getAllOrders(limitOfResults=25): Observable<serverResponse> {
    return this.http.get<serverResponse>(this.url + 'orders', {
      params: {
        limit: limitOfResults.toString()
      }
    });
  }


  getSingleOrder(orderId: Number) {
    return this.http.get<ProductResponseModel[]>(`${this.url}orders/${orderId}`).toPromise();
  }
}

interface ProductResponseModel {
  id: Number;
  title: String;
  description: String;
  price: Number;
  quantityOrdered: Number;
  image: String;
}
